/**
 * Get Manufacturing Item identifiers from an engineering item ID
 * @param {object} headers
 * @param {string} engItemId
 * @returns {Promise<string[]>} - Array of manufacturing item identifiers
 */
async function getManufacturingItems(headers, engItemId) {
  const url = `${ENOVIA_BASE_URL}/resources/v1/modeler/dsmfg/invoke/dsmfg:getMfgItemsFromEngItem`;
  const body = [engItemId];
 
  try {
    const response = await callEnoviaWebService("POST", url, body, headers);
    if (response.output.totalItems > 0 && Array.isArray(response.output.member)) {
        return response.output.member.map(item => item.identifier);
    }
    return [];
  } catch (error) {
    console.error("Error fetching manufacturing items:", error);
    return [];
  }
}
 
/**
 * Get detailed info for a manufacturing item identifier
 * @param {object} headers
 * @param {string} identifier
 * @returns {Promise<object|null>} - Details with name, title, type, createdFrom, identifier
 */
async function getMfgItemDetails(headers, identifier) {
  const url = `${ENOVIA_BASE_URL}/resources/v1/modeler/dsmfg/dsmfg:MfgItem/${identifier}?$mask=dsmfg:MfgItemMask.Details`;
 
  try {
    const response = await callEnoviaWebService("GET", url, "", headers);
    console.log("Response from Manufacturing Item URL:", response);
    console.log("Response totalItems:", response.output.totalItems);
    console.log("Response member:", response.output.member);
    if ( response &&
        response.output &&
        response.output.totalItems > 0 &&
        Array.isArray(response.output.member) &&
        response.output.member.length > 0) {
      const item = response.output.member[0];
      console.log("item:", item);
      return {
        identifier: item.id,
        name: item.name,
        title: item.title,
        type: item.type,
        createdFrom: item.createdFrom || null
      };
    }
    return null;
  } catch (error) {
    console.error(`Error fetching details for identifier ${identifier}:`, error);
    return null;
  }
}
 
/**
 * Get plant class IDs assigned to a manufacturing item (filtering where PlantAssignmentClass is true)
 * @param {object} headers
 * @param {string} mfgIdentifier
 * @returns {Promise<string[]>} - Array of plant ClassIDs
 */
async function getPlantClassIDs(headers, mfgIdentifier) {
  const url = `${ENOVIA_BASE_URL}/resources/v1/modeler/dslib/dslib:CategorizationClassifiedItem/${mfgIdentifier}?$mask=dslib:ClassificationAttributesMask`;
 
  try {
    const response = await callEnoviaWebService("GET", url, "", headers);
    console.log("Response from Plant Class IDs URL:", response);
    console.log("Response totalItems:", response.output.totalItems);
    if (
      response.output.totalItems > 0 &&
      response.output.member &&
      response.output.member.length > 0 &&
      response.output.member[0].ClassificationAttributes &&
      Array.isArray(response.output.member[0].ClassificationAttributes.member)
    ) {
      const classMembers = response.output.member[0].ClassificationAttributes.member;
      return classMembers
        .filter(classItem => {
          const plantAssignmentAttr = (classItem.Attributes || []).find(attr => attr.name === "PlantAssignmentClass");
          return plantAssignmentAttr && plantAssignmentAttr.value === true;
        })
        .map(classItem => classItem.ClassID);
    }
    return [];
  } catch (error) {
    console.error(`Error fetching plant class IDs for ${mfgIdentifier}:`, error);
    return [];
  }
}
 
/**
 * Get Plant title by ClassID
 * @param {object} headers
 * @param {string} classId
 * @returns {Promise<string|null>} - Plant title or null if not found
 */
async function getPlantTitleByClassID(headers, classId) {
  const url = `${ENOVIA_BASE_URL}/resources/v1/modeler/dslib/dslib:Class/${classId}`;
 
  try {
    const response = await callEnoviaWebService("GET", url, "", headers);
    if (
      response.output.totalItems > 0 &&
      response.output.member &&
      response.output.member.length > 0
    ) {
      return response.output.member[0].title || null;
    }
    return null;
  } catch (error) {
    console.error(`Error fetching plant title for class ID ${classId}:`, error);
    return null;
  }
}
 
/**
 * Get all assigned plant titles for a manufacturing item identifier
 * @param {object} headers
 * @param {string} mfgIdentifier
 * @returns {Promise<string[]>} - Array of plant titles
 */
async function getAssignedPlants(headers, mfgIdentifier) {
  try {
    const classIDs = await getPlantClassIDs(headers, mfgIdentifier);
    if (classIDs.length === 0) return [];
 
    const plantTitles = [];
    for (const classId of classIDs) {
        const existing = ClassDetails.find(item => item.classid === classId);
        
        if (existing) {
            // If classId is already in ClassDetails, push its title to plantTitles
            plantTitles.push(existing.title);
        } else {
            // If not, fetch title, then push to both ClassDetails and plantTitles
            const title = await getPlantTitleByClassID(headers, classId);
            if (title) {
            ClassDetails.push({ classid: classId, title });
            plantTitles.push(title);
            }
        }
        }
    return plantTitles;
  } catch (error) {
    console.error(`Error fetching assigned plants for ${mfgIdentifier}:`, error);
    return [];
  }
}
 
/**
 * Expand children manufacturing items for a given identifier
 * Exclude those of type "DELFmiFunctionIdentifiedInstance"
 * Add assigned plants info for each child
 * @param {object} headers
 * @param {string} identifier
 * @returns {Promise<object[]>} - Array of child objects with id, name, type, title, and AssignedPlants
 */
async function getMFGChilders(headers, identifier) {
    const url = `${ENOVIA_BASE_URL}/resources/v1/modeler/dsmfg/dsmfg:MfgItem/${identifier}/expand`;
    const body = {
      expandDepth: 1,
      withPath: true
    };
     
 
    try {
      const response = await callEnoviaWebService("POST", url, body, headers);
 
      if (
        response.status &&
        response.output &&
        Array.isArray(response.output.member)
      ) {
        // Filter children (exclude DELFmiFunctionIdentifiedInstance)
        const children = response.output.member.filter(
          item => item.type && item.type !== "DELFmiFunctionIdentifiedInstance"
        );
 
        // Enrich each child with AssignedPlants
        const enrichedChildren = await Promise.all(
          children.map(async child => {
            const assignedPlants = await getAssignedPlants(headers, child.id);
            return {
              id: child.id,
              type: child.type,
              name: child.name,
              title: child.title || null,
              AssignedPlants: assignedPlants
            };
          })
        );
 
        return enrichedChildren;
      }
      return [];
    } catch (error) {
      console.error(`Error fetching children for identifier ${identifier}:`, error);
      return [];
    }
  }
 
 
/**
 * Main method that prepares the output data
 * @param {object} headers
 * @param {string} engItemId - Engineering Item ID
 * @param {string} droppedMfgID - Manufacturing Item ID to flag as currentObject
 * @returns {Promise<object[]>} - Array of objects with manufacturing details and assigned plants and children
 */
export async function prepareDataOutput(headers, droppedMfgID, engItemId) {
  try {
    const identifiers = await getManufacturingItems(headers, engItemId);
    console.log("Manufacturing Identifiers:", identifiers);
    if (identifiers.length === 0) return [];
 
    const detailsArray = await Promise.all(
      identifiers.map(id => getMfgItemDetails(headers, id))
    );

    console.log("Manufacturing Details:", detailsArray);
 
    const items = detailsArray.filter(item => item !== null);
 
    // Determine which items are parents (no createdFrom reference from others)
    const createdFromSet = new Set(
      items
        .map(item => item.createdFrom)
        .filter(Boolean)
        .map(cf => cf.split(" ")[0])
    );

    console.log("Created From Set:", createdFromSet);
 
    const result = [];
 
    for (const item of items) {
      const isParent = createdFromSet.has(item.name);
      const currentObject = item.identifier === droppedMfgID;
      console.log(`Processing item: ${item.name}, isParent: ${isParent}, currentObject: ${currentObject}`);
 
      // Get assigned plants for each manufacturing item
      const assignedPlants = await getAssignedPlants(headers, item.identifier);
      console.log(`Assigned Plants for Hello:`, assignedPlants);
 
      // Expand children if parent or currentObject
      let children = [];
      if (isParent || currentObject) {
        children = await getMFGChilders(headers, item.identifier);
      }
 
      result.push({
        identifier: item.identifier,
        name: item.name,
        title: item.title,
        type: item.type,
        isParent,
        currentObject,
        AssignedPlants: assignedPlants,
        children
      });
    }

    console.log("Prepared Data Output:", result);
 
    return result;
  } catch (error) {
    console.error("Error in prepareDataOutput:", error);
    return [];
  }
}
 
 
//Plants for popup
export async function preparePlantLists(preparedDataOutput, engItemId) {
    // 1. Get plants assigned to the engineering item
    const engItemPlants = await getAssignedPlants(engItemId);
 
    // 2. Find the current MFG item (where currentObject === true)
    const currentMfgItem = preparedDataOutput.find(mfg => mfg.currentObject === true);
 
    if (!currentMfgItem) {
      console.warn("Current MFG item not found in prepared data output.");
      return {
        AssignedPlants: [],
        AvailablePlants: engItemPlants
      };
    }
 
    // Assigned plants of current MFG
    const assignedPlants = currentMfgItem.AssignedPlants || [];
 
    // 3. Collect plants assigned to all other MFG items except the parent MFG item
    const otherPlants = [];
 
    preparedDataOutput.forEach(mfgItem => {
      if (!mfgItem.isParent) {  // exclude parent MFG plants
        const plants = mfgItem.AssignedPlants || [];
        plants.forEach(plant => {
          if (!otherPlants.includes(plant)) {
            otherPlants.push(plant);
          }
        });
      }
    });
 
    // 4. Calculate available plants: engItemPlants - otherPlants
    const availablePlants = engItemPlants.filter(plant => !otherPlants.includes(plant));

    console.log("Assigned Plants:", assignedPlants);
    console.log("Available Plants:", availablePlants);


    return {
      AssignedPlants: assignedPlants,
      AvailablePlants: availablePlants
    };
  }